.logo {
  @media (max-width: @mobile-width-only) {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 21px;
  }

  &[href]:hover {
    opacity: 0.6;
  }

  &[href]:active {
    opacity: 0.3;
  }

  &[href]:focus {
    .focus-outline;
    outline-offset: -3px;

    @media (min-width: @tablet-width) {
      outline-offset: 2px;
    }
  }
}

.logo__icon {
  display: block;

  @media (min-width: @tablet-width) {
    width: 113px;
    height: 46px;
  }

  @media (min-width: @desktop-width) {
    width: 149px;
    height: 72px;
  }
}
